import _ from "lodash";

export const findSelectedOptionNode = (optionsNodes, conditionAsValue) => {
  var currentOptNode;
  for (var i = 0; i < optionsNodes?.length; i++) {
    if (optionsNodes[i]?.value === conditionAsValue) {
      currentOptNode = optionsNodes[i];
    }
  }
  return currentOptNode;
};

// 文本控件
const inputTextWidget = {
  name: "input.text",
  type: "input",
  attrs: {
    type: "text",
    placeholder: "{默认值}",
  },
  childNode: [],
  style: "{内联样式}",
  class: `custom_input-text`,
};

// 下拉控件
const selectWidget = {
  name: "select",
  type: "select",
  attrs: {
    placeholder: "{默认值}",
  },
  childNode: [
    {
      name: "option",
      type: "option",
      attrs: {
        value: "{值}",
        selected: false,
      },
      childNode: "{文本}",
      style: "{内联样式}",
      class: `custom_select--option`,
    },
  ],
  style: "{内联样式}",
  class: `custom_select`,
};

export const widgetList = {
  inputText: inputTextWidget,
  select: selectWidget,
};

// 生成dom节点
export const generateElement = (element) => {
  var attrs = _.reduce(
    _.entries(element?.attrs),
    (preItem, [key, value]) => {
      return preItem + `${key}='${value}' `;
    },
    ""
  );
  var innerText = "";
  _(element?.childNode).forEach(
    (childNodeEle) => (innerText += generateElement(childNodeEle))
  );
  return `<${element.type} ${attrs} ${
    element?.style ? "style=" + element.style : ""
  } ${element?.class ? "class=" + element?.class : ""}>
  ${
    _.isArray(element?.childNode) && element?.childNode?.length > 0
      ? innerText
      : element?.text ?? element?.childNode
  }
  </${element?.type}>`;
};

// 根据节点类型替换处理为文本展示标签
function replaceNode(node) {
  var textNode = {
    name: "div",
    type: "div",
    childNode: "",
  };
  var isConvert = true;
  switch (node.type) {
    case "input":
      textNode.childNode = node?.attrs?.value;
      break;
    case "select":
      var selectChildNode = _.find(
        node?.childNode,
        (childNodeItem) => childNodeItem?.attrs.selected
      );
      console.log(node?.childNode, "==selectChildNode==");
      textNode.text = selectChildNode?.text;
      break;
    default:
      isConvert = false;
      break;
  }
  return isConvert ? textNode : node;
}

// 递归替换表单标签为div文本标签
const execReplaceFormConvertText = (childNode) => {
  return _.map(childNode, (elementNode) => {
    return {
      ...elementNode,
      childNode:
        _.isArray(elementNode?.childNode) && elementNode?.childNode?.length
          ? replaceFormConvertText(elementNode)
          : [],
      ...replaceNode(elementNode),
    };
  });
};

export const replaceFormConvertText = (element) => {
  return {
    ...element,
    childNode: execReplaceFormConvertText(element.childNode),
  };
};

// html文件
export const generateHtmlText = (nodeContext) => {
  return `
    <style>
      .${nodeContext.class} {
        // 编写你的样式
      }
    </style>
    ${generateElement(nodeContext)}
  `;
};

// 下载控件txt文件
export const downTxt = (name, value) => {
  let filename = `${name}.txt`; //注意最好设置后缀.txt
  let elem = document.createElement("a"); //创建a标签
  elem.setAttribute(
    "href",
    "data:text/plain;charset=utf-8," + encodeURIComponent(value)
  ); //设置下载文件内容
  elem.setAttribute("download", filename); //设置下载文件名
  elem.style.display = "none"; //设置a标签默认隐藏
  document.body.appendChild(elem); //添加至dom层
  elem.click(); //执行点击下载事件
  document.body.removeChild(elem); //使用下载后移除a标签
};
