<template>
  <div>
    <CodeEditor
      :language_selector="true"
      :display_language="true"
      v-model="tempHtml"
      :languages="[['html']]"
      height="200px"
      width="100%"
      :autofocus="true"
    />
    <a-row style="margin: 10px">
      <a-col :span="2">
        <a-button type="primary"> 预览区域 </a-button>
      </a-col>
      <a-col :span="3" offset="1">
        <a-button type="primary" @click="previewHandle"> 读取提交字符串显示 </a-button>
      </a-col>
      <a-col :span="3" offset="1">
        <a-button type="primary" @click="visibleWidgetModal = true"> 查看可下载控件 </a-button>
      </a-col>
    </a-row>
    <div></div>
    <div v-html="tempHtml" class="html-view" ref="htmlView"></div>
    <a-modal v-model="visible" title="模板预览">
      <div class="preview-view" v-html="previewHtml"></div>
    </a-modal>
    <a-modal v-model="visibleWidgetModal" title="模板预览">
      <a-row style="margin: 10px">
        <a-col :span="5" offset="3" v-for="key in widgetKeyList" :key="key">
          <a-button type="primary" @click="execDownPipe(key)"> 控件({{ key }}) </a-button>
        </a-col>
      </a-row>
    </a-modal>
  </div>
</template>

<script>
import CodeEditor from 'simple-code-editor'
import _ from 'lodash'
import {
  findSelectedOptionNode,
  widgetList,
  generateHtmlText,
  downTxt as downTxtService,
  replaceFormConvertText,
  generateElement
} from './tools'
import htmlStrParser from './htmlStrParser'

export default {
  components: {
    CodeEditor
  },
  data() {
    return {
      visible: false, // 模板预览
      visibleWidgetModal: false, // 控件下载
      tempHtml: '', // 模版html
      previewHtml: '', // 预览html
      widgetKeyList: _.keys(widgetList)
    }
  },
  mounted() {
    // this.initProxyEvent();
  },
  methods: {
    // 深度查找节点
    deepFindFormNode(node) {
      var stack = []
      var innerNodeList = []
      if (node) {
        stack.push(node)
        while (stack.length) {
          var item = stack.shift()
          var childNodes = item.childNodes
          innerNodeList.push(item)
          if (childNodes.length) {
            for (var i = 0; i < childNodes.length; i++) {
              if (childNodes[i].nodeType === 1) {
                // 判断是标签节点
                stack.push(childNodes[i])
              }
            }
          }
        }
        return innerNodeList
      }
    },
    forEachFormNode(formNode = []) {
      for (var nodeIndex = 0; nodeIndex < formNode.length; nodeIndex++) {
        this.setFormNodeValue(formNode[nodeIndex])
      }
    },
    // 设置节点绑定值
    setFormNodeValue(node) {
      switch (node.nodeName) {
        case 'INPUT':
          node.setAttribute('value', node?.value)
          break
        case 'SELECT':
          var selectOptionNode = findSelectedOptionNode(node?.childNodes, node?.value)
          selectOptionNode.setAttribute('selected', true)
          break
      }
    },
    // setBaseStyleText(html) {
    //   return `<style>input,select{outline:none;}input[readonly = true]{outline:none;}</style>${html}`;
    // },
    // 预览模板
    previewHandle() {
      console.log(this.$refs.htmlView.childNodes)
      var parentNodes = this.$refs.htmlView
      var formBindAbleNodes = this.deepFindFormNode(parentNodes) // 寻找需要绑定的节点
      this.forEachFormNode(formBindAbleNodes) // 设置表单节点绑定
      // 模拟从服务器拿来节点字符串
      var previewHtml = this.analysisFormToConvertTextNode(this.$refs.htmlView.innerHTML)
      console.log(previewHtml, '==previewHtml==')
      this.previewHtml = previewHtml
      this.visible = true
    },
    // 解析form特殊表单转换成div显示节点（select不能使用readonly）
    analysisFormToConvertTextNode(innerHTML) {
      const htmlTreeNode = htmlStrParser(innerHTML)
      const replaceHtmlTreeNode = replaceFormConvertText(htmlTreeNode)
      console.log(htmlTreeNode, '==htmlTreeNode==')
      return generateElement(replaceHtmlTreeNode)
    },
    // 执行控件下载
    execDownPipe(widgetKey) {
      var currentWidget = widgetList[widgetKey]
      var value = generateHtmlText(currentWidget)
      downTxtService(currentWidget.name, value)
    }
  }
}
</script>
<style lang="scss" scoped>
.preview-title {
  margin: 10px 0;
}
.html-view {
  width: 100%;
  min-height: 80vh;
  border: 1px solid #fff;
}

.preview-view {
  select[readonly] option {
    display: none;
  }
}
</style>
